/* Google Fonts - Poppins */
@import url('https://fonts .googleapis.com/css2?family=Poppins:wght@300; 400;500;600;700&display=swap');

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Poppins', sans -serif;
}
 
.body{
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  background: #7d2ae8;
}

.container{
  display: flex;
  width: 420px;
  padding: 58px 0;
  /* border-radius: 50%; */
  background: #fff;
  row-gap: 30px;
  flex-direction: column;
  align-items: center;
}

.circular-progress{
  position: relative;
  height: 250px;
  width: 250px;
  border-radius: 50%;
  background: conic-gradient(#7d2ae8 5.5deg, #ededed 0deg);
  display: flex;
  align-items: center;
  justify-content: center;
}

.circular-progress::before{
  content: "";
  position: absolute;
  height: 210px;
  width: 210px;
  border-radius: 50%;
  background-color: #f0ff;
}

.progress-value{
  position: relative;
  font-size: 40px;
  font-weight: 600;
  color: #7d2ae8;
}

.text {
  font-size: 30px;
  font-weight: 500;
  color: #606068;
}


.btn{
  display: flex;
  justify-content: space-between;
}

button{
  padding: 10px;
  margin-left: 10px;
  cursor: pointer;
}